<template>
  <form>
    <div style="display: flex; justify-content: center; align-content: center;">
      <div style="width: 200px; height: 200px; position: relative;">
        <canvas width="400" height="400" ref="canvas" class="canvas"></canvas>
        <label for="file-image" style="position:absolute;top:0;left:0;right:0;bottom:0;">
          <input type="file" ref="file" id="file-image" @change="onChangeImage" accept="image/jpg,image/png,image/gif" style="display:none;" />
        </label>
      </div>
    </div>
  </form>
</template>

<script>
export default {
  props: {
    borderWidth: {
      type: Number,
      default: 50
    },
    firstText: {
      type: String,
      default: "いのちのクラハ"
    },
    secondText: {
      type: String,
      default: "❤❤❤︎"
    }
  },
  watch: {
    borderWidth() {
      this.draw()
    },
    firstText() {
      this.draw()
    },
    secondText() {
      this.draw()
    }
  },
  methods: {
    draw() {
      const borderWidth = this.borderWidth;
      const path = new Path2D("M 100 50 C 100 85.7361 85.7361 100 50 100 C 14.2639 100 0 85.7361 0 50 C 0 14.2639 14.2639 0 50 0 C 85.7361 0 100 14.2639 100 50 Z")
      this.ctx.beginPath()
      this.ctx.clearRect(0, 0, 200, 200)
      this.ctx.setTransform(4, 0, 0, 4, 0, 0)
      this.ctx.clip(path)
      this.ctx.setTransform(2, 0, 0, 2, 0, 0)
      if (this.image.complete) {
        const canvasWidth = this.ctx.canvas.width / 2
        const canvasHeight = this.ctx.canvas.height / 2
        const imageWidth = this.image.width
        const imageHeight = this.image.height
        const imageAspect = imageWidth / imageHeight
        if (imageAspect >= 1.0) {
          // 横長
          const imageAdjustedWidth = (canvasWidth - borderWidth) * imageAspect
          const imageAdjustedHeight = canvasHeight - borderWidth
          const imageAdjustedLeft = borderWidth / 2 - (imageAdjustedWidth - imageAdjustedHeight) / 2
          const imageAdjustedTop = borderWidth / 2
          this.ctx.drawImage(this.image, imageAdjustedLeft, imageAdjustedTop, imageAdjustedWidth, imageAdjustedHeight)
        } else {
          // 縦長
          const imageAdjustedWidth = canvasWidth - borderWidth
          const imageAdjustedHeight = (canvasHeight - borderWidth) / imageAspect
          const imageAdjustedLeft = borderWidth / 2
          const imageAdjustedTop = borderWidth / 2 - (imageAdjustedHeight - imageAdjustedWidth) / 2
          this.ctx.drawImage(this.image, imageAdjustedLeft, imageAdjustedTop, imageAdjustedWidth, imageAdjustedHeight)
        }
      }
      //this.ctx.arc(100/2, 100/2, borderWidth, 0, Math.PI * 2)
      this.ctx.setTransform(4, 0, 0, 4, 0, 0)
      if (borderWidth > 0) {
        this.ctx.lineWidth = borderWidth / 2;
        this.ctx.strokeStyle = "#00ff00"
        this.ctx.stroke(path)
      }

      this.ctx.fillStyle = "#00ff00"
      this.ctx.rect(0, 75, 200, 100)
      this.ctx.fill()

      this.ctx.setTransform(2, 0, 0, 2, 0, 0)
      this.ctx.font = "bold 20px Kokoro"
      this.ctx.textAlign = "center"
      this.ctx.strokeStyle = "#ffffff"
      this.ctx.lineWidth = 2;
      this.ctx.strokeText(this.firstText, 100, 170)
      this.ctx.strokeText(this.secondText, 100, 190)
      this.ctx.fillStyle = "#ff0000"
      this.ctx.fillText(this.firstText, 100, 170)
      this.ctx.fillText(this.secondText, 100, 190)

      console.debug(this.image.complete)
    },
    onChangeImage() {
      const files = this.$refs.file;
      const fileImg = files.files[0];
      if (fileImg.type.startsWith("image/")) {
        this.image.src = window.URL.createObjectURL(fileImg);
      }
    },
    onDownload() {
      // let link = document.createElement("a");
      // link.href = this.$refs.canvas.toDataURL("image/png");
      // link.download = "icon.png";
      // link.click();
      return this.$refs.canvas.toDataURL("image/png");
    }
  },
  mounted() {
    // mounted 以降で canvas の DOM にアクセスできる
    // CreateJS などを使うときにも、ここで canvas と紐付ける
    // console.log(this.$el)
    this.ctx = this.$refs.canvas.getContext('2d')
    this.image = new Image();
    this.image.src = '/select.png';  // 画像のURLを指定
    this.image.onload = () => {
      this.draw()
    }
    // WebFont用に再ロードする
    this.interval = setInterval(() => {
      this.draw()
    }, 1000)
    this.draw()
  },
  destroyed() {
    clearInterval(this.interval)
  }
}
</script>

<style scoped>
.wf-kokoro { font-family: "Kokoro",serif; }
.canvas {
  /*border: 1px solid #000;*/
  width: 200px;
  height: 200px;
}
</style>
