<template>
  <div class="hello">
    <div class="example" v-show="!downloading">
      <p>❤いのちのクラハ アイコンメーカー❤</p>
      <MyCanvas ref="myCanvas" :border-width="borderWidth" :first-text="firstText" :second-text="secondText"/>
      <p><input type="range" min="0" max="100" v-model.number="borderWidth"></p>
      <p><input type="text" v-model="firstText"></p>
      <p><input type="text" v-model="secondText"></p>
      <p><button @click="onDownload">ダウンロード</button></p>
    </div>
    <div v-show="downloading">
      <p>❤下の画像を長押しして保存してください❤︎</p>
      <p><img ref="downloadImg" width="200" height="200"></p>
      <p><button @click="onBack">戻る</button></p>
    </div>
  </div>
</template>

<script>
import MyCanvas from './MyCanvas.vue'
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  components: {
    MyCanvas
  },
  data() {
    return {
      borderWidth: 50,
      firstText: "いのちのクラハ",
      secondText: "❤︎❤︎❤︎",
      downloading: false
    }
  },
  methods: {
    onDownload() {
      this.$refs.downloadImg.src = this.$refs.myCanvas.onDownload()
      this.downloading = true
    },
    onBack() {
      this.downloading = false
    }
  }
}
</script>

